* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  font-family: inherit;
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
}

ul,
li {
  list-style: none;
}

body {
  font-family: 'Montserrat', sans-serif;
  background: linear-gradient(124.53deg, #fc3f5f 0%, #7d61ff 100.32%);
}
